<template>
  <div id="downloadEchart">
    <el-tabs v-model="downloadEchartName" @tab-click="handleClick">
      <!--App-->
      <el-tab-pane label="App" name="1"></el-tab-pane>
      <!---->
<!--      <el-tab-pane label="" name="2"></el-tab-pane>-->
      <!---->
      <el-tab-pane label="" name="2"></el-tab-pane>
      <!---->
      <el-tab-pane label="" name="3"></el-tab-pane>
      <!---->
      <el-tab-pane label="" name="4"></el-tab-pane>

    </el-tabs>
    <div id="pieBox" style="width:803px;height:432px;"></div>
    <p class="sec"><i>{{title}}</i>{{main}}</p>
    <div class="op">

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
var echarts = require('echarts');
export default {
  props: ["comid","compName"],
  data () {
    return {
      compaName:"",
      downloadEchartName:'1',
      title:"",
      main:"App",
      xdata:["20170322",
        "20170323",
        "20170324",
        "20170325",
        "20170326",
        "20170327",
        "20170328",
        "20170329",
        "20170330",
        "20170331",
        "20170401",
        "20170402",
        "20170403",
        "20170404",
        "20170405",
        "20170406",
        "20170407",
        "20170408",
        "20170409",
        "20170410",
        "20170411",
        "20170412",
        "20170413",
        "20170414",
        "20170415",
        "20170416",
        "20170417",
        "20170418",
        "20170419",
        "20170420",
        "20170421",
        "20170422",
        "20170423",
        "20170424",
        "20170425",
        "20170426",
        "20170427",
        "20170428",
        "20170429",
        "20170430",
        "20170501",
        "20170502",
        "20170503",
        "20170504",
        "20170505",
        "20170506",
        "20170507",
        "20170508",
        "20170509",
        "20170510",
        "20170511",
        "20170512",
        "20170513",
        "20170514",
        "20170515",
        "20170516",
        "20170517",
        "20170518",
        "20170519",
        "20170520",
        "20170521",
        "20170522",
        "20170523",
        "20170524",
        "20170525",
        "20170526",
        "20170527",
        "20170528",
        "20170529",
        "20170530",
        "20170531",
        "20170601",
        "20170602",
        "20170603",
        "20170604",
        "20170605",
        "20170606",
        "20170607",
        "20170608",
        "20170609",
        "20170610",
        "20170611",
        "20170612",
        "20170613",
        "20170614",
        "20170615",
        "20170616",
        "20170617",
        "20170618",
        "20170619"],//
      download:{
        ydataTotal:[],//
        ydataAverage:[]//
      },//APP
      dau:{
        ydataTotal:[],
        ydataAverage:[]
      },//
      pv:{
        ydataTotal:[],
        ydataAverage:[]
      },//
      uv:{
        ydataTotal:[],
        ydataAverage:[]
      },//
      time:{
        ydataTotal:[],
        ydataAverage:[]
      },//
    }
  },
  computed:{

  },
  methods: {
    getChart(data){
      for(let i=0; i<data.length; i++){
          if(data[i].project_views!=""){
              return JSON.parse(data[i].project_views);
          }
      }
    },//json
    getAverage(data){
        let arr=[];
      let index=this.xdata.length;
        for(let i=0; i<index; i++){
          arr[i]=data;
        };
        return arr;
    },//
    getTotal(data){
      for(let i=0; i<data.length; i++){
        if(data[i]=="") data[i]=0;
      };
      return data
    },//
    handleClick(tab, event) {
      let index=tab.index
//      console.log(tab.index);
      switch (index){
        case '0':
          this.getdownload();
          break;
/*        case '1':
          this.getdau();
          break;*/
        case '1':
          this.getpv();
          break;
        case '2':
          this.getuv();
          break;
        case '3':
          this.gettime();
          break;
        default:
          alert("")
          break;
      };
    },
    eChart(xdata,ydata1,ydata2){
      let myChart = echarts.init(document.getElementById('pieBox'));
      let option = {
          title: {},
          color: ['#28DC41', '#009eff'],
          tooltip: {
            trigger: 'axis',
            backgroundColor:'rgba(31,45,61,0.85);',
            padding: [10],
            transitionDuration:0.5,
            extraCssText:'border-radius:8px;',
            formatter: function(e){
              let res = e[0].axisValue.slice(0,4)+'.'+e[0].axisValue.slice(4,6)+'.'+e[0].axisValue.slice(6,8)+'</br>'
              res+=':'+e[0].value+'</br>'
              res+=':'+e[1].value
              return res;
            }
          },
          legend: {
            data: ['','']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
//        dataZoom:{
//          orient:"vertical", //
//          show:true, //
//          start:0, //20%
//          end:100,  //60%
//        },
        toolbox: {

          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            splitLine:{
              show:true
            },
            data: xdata
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            name: '',
            type: 'line',
            smooth: true,
            symbolSize: 8,
            data: ydata2
          }, {
            name: '',
            type: 'line',
            symbolSize: 8,
            smooth: true,
            data: ydata1
          }]
        };
      myChart.setOption(option);
      },
    getdownload(){
      this.title="",
      this.main="App";
      this.eChart(this.xdata,this.download.ydataTotal,this.download.ydataAverage);
    },//
    getdau(){
      this.title="",
      this.main="";
      this.eChart(this.xdata,this.dau.ydataTotal,this.dau.ydataAverage);
    },//
    getpv(){
      this.title="(PV)",
      this.main=",,,.,."
      this.eChart(this.xdata,this.pv.ydataTotal,this.pv.ydataAverage);
    },//
    getuv(){
      this.title="(UV)",
      this.main=",,,.,."
      this.eChart(this.xdata,this.uv.ydataTotal,this.uv.ydataAverage);
    },//
    gettime(){
      this.title="",
      this.main=",,."
      this.eChart(this.xdata,this.time.ydataTotal,this.time.ydataAverage);
    },//
    getCrawlerProject(){
      this.$http.post(this.URL.getCrawlerProject, {
        user_id: sessionStorage.user_id,
        com_id: this.comid
      })
        .then(res => {


            let data=this.getChart(res.data.data).data;
//            console.log(data);
            this.xdata=data.three_month;
            this.download.ydataTotal=this.getAverage(data.total_download_mid);
            this.download.ydataAverage=this.getTotal(data.total_download[0].value);

/*            this.dau.ydataTotal=this.getAverage(data.dau_mid);
            this.dau.ydataAverage=this.getTotal(data.dau[0].value);*/

            this.pv.ydataTotal=this.getAverage(data.pv_mid);
            this.pv.ydataAverage=this.getTotal(data.pv[0].value);

            this.uv.ydataTotal=this.getAverage(data.uv_mid);
            this.uv.ydataAverage=this.getTotal(data.uv[0].value);

            this.time.ydataTotal=this.getAverage(data.time_mid);
            this.time.ydataAverage=this.getTotal(data.time[0].value);

        })
        .catch(err => {
          console.log(err);
        })
    },//

  },
  //Echart
  mounted(){
    this.getCrawlerProject();

    this.getdownload();
  },
  watch : {
    comid : function(e){
      this.download.ydataTotal=[];
      this.download.ydataAverage=[];
      this.pv.ydataTotal=[];
      this.pv.ydataAverage=[];
      this.uv.ydataTotal=[];
      this.uv.ydataAverage=[];
      this.time.ydataTotal=[];
      this.time.ydataAverage=[];
      this.getCrawlerProject();
      this.eChart(this.xdata,this.download.ydataTotal,this.download.ydataAverage);
    },//id
    compName: function(e){
      this.compaName=e;
      this.getdownload();

    },//

  },
  created(){
    this.getCrawlerProject();
  },
}
</script>

<style lang="less">
  #downloadEchart{
    position: relative;
    .op{
      width: 169px;
      height: 23px;
      opacity: 0;
      position: absolute;
      left: 317px;
      top: 55px;
    }
  }
  .sec{
    position: relative;
    font-size:13px;
    color:#5e6d82;
    margin-top:48px;
    line-height: 23px;
    i{
      color:#475669;
      font-weight: bolder;
      line-height: 23px;

    }
  }

</style>



